      // userItems: [],
      // accountingItems: [],
    };
  },

  filters: {
    filtermoney(num) {
      const n = Number(num);
      return `$${n.toFixed(0).replace(/./g, (c, i, a) => {
        const currency =
          i && c !== "." && (a.length - i) % 3 === 0
            ? `, ${c}`.replace(/\s/g, "")
            : c;
        return currency;
      })}`;
    },
    backYear(val) {
      return dayjs(val).format("YYYY");
    },
    backMonth(val) {
      return dayjs(val).format("MM");
    },
  },

  async mounted() {
    await this.setAllData();
  },

  methods: {
    async setAllData() {
      this.cData = JSON.parse(sessionStorage.getItem("cData"));
      this.pData = JSON.parse(sessionStorage.getItem("pData"));
      let items = JSON.parse(this.$route.query.data);
      // console.log(1);
      this.fees = await this.$api.get('fee')
      this.items = await Promise.all(
        items.map(async (i) => {
          return {
            ...i,
            filterAccounting: await this.filterAccounting(i),
          };
        })
      );

      // console.log(4, items);
      // this.getAccounting(); //取得會計帳(所有的)
      // this.getUser(); //取得所有病歷(所有的)
      this.image = this.$store.state.base_url + "images/" + this.cData.seal;
    },
    //回傳指定病歷的會計帳
    async filterAccounting(list) {
      // console.log(2, list);
      try {
        let searhKey = {
          key: `user_snkey='${list.user_snkey}'`,
        };

        let url = `general/searchByWhere/${this.$store.state.databaseName}/accounting`;
        let matchData = await this.$api.options(url, searhKey);
        matchData = matchData.filter(
          (i) => i.user_snkey == list.user_snkey && i.date.includes(list.month)
        );
        matchData = matchData.map(i=>{
          let fee = this.fees.find(fee=>fee.snkey == i.fee_snkey)
          return {
            ...i,
            name:fee.name,
            kind:fee.kind
          }
        })

        //增加合併某些常態發生的帳,所以在處理時,要把不合併的帳都先挑出來,再把各項需要合併的帳算完後加進去~
        var spAccounting = [
          "驗血糖/次",
          "傷口換藥-大(次)",
          "傷口換藥-中(次)",
          "傷口換藥-小(次)",
          "傷口換藥-XS (次)",
        ];
        var printData = matchData.filter((i) => {
          return !spAccounting.includes(i.name);
        });

        spAccounting.forEach((accountingitem) => {
          const matchAccountings = matchData.filter((i) => {
            return i.name == accountingitem;
          });

          const startDayArr = matchAccountings.map((i) => {
            return dayjs(i.start_day);
          });
          const endDayArr = matchAccountings.map((i) => {
            return dayjs(i.start_day);
          });

          if (matchAccountings.length > 0) {
            const matchAccountingCounts = matchAccountings.reduce(
              (total, i) => {
                return parseInt(total) + parseInt(i.quantity);
              },
              0
            );

            printData.push({
              //  date:dayjs(Math.max(...endDayArr)).format("YYYY-MM-DD"),
              name: accountingitem,
              kind: matchAccountings[0].kind,
              start_day: dayjs(Math.min(...startDayArr)).format("YYYY-MM-DD"),
              end_day: dayjs(Math.max(...endDayArr)).format("YYYY-MM-DD"),
              unit: matchAccountings[0].unit,
              price: matchAccountings[0].price,
              quantity: matchAccountingCounts,
            });
          }
        });
        //增加合併某些常態發生的帳,所以在處理時,要把不合併的帳都先挑出來,再把各項需要合併的帳算完後加進去~end

        printData = printData.sort(function (a, b) {
          return Number(a.price) < Number(b.price) ? 1 : -1;
        });

        // console.log(3, list, printData);
        return printData;
      } catch (err) {
        console.log(err);
      }

      // var matchData = this.accountingItems.filter((i) => {
      //   return i.user_snkey == list.user_snkey && i.date.includes(list.month);
      // });

      // //增加合併某些常態發生的帳,所以在處理時,要把不合併的帳都先挑出來,再把各項需要合併的帳算完後加進去~
      // var spAccounting = [
      //   "驗血糖/次",
      //   "傷口換藥-大(次)",
      //   "傷口換藥-中(次)",
      //   "傷口換藥-小(次)",
      //   "傷口換藥-XS (次)",
      // ];
      // var printData = matchData.filter((i) => {
      //   return !spAccounting.includes(i.name);
      // });

      // spAccounting.forEach((accountingitem) => {
      //   const matchAccountings = matchData.filter((i) => {
      //     return i.name == accountingitem;
      //   });

      //   const startDayArr = matchAccountings.map((i) => {
      //     return dayjs(i.start_day);
      //   });
      //   const endDayArr = matchAccountings.map((i) => {
      //     return dayjs(i.start_day);
      //   });

      //   if (matchAccountings.length > 0) {
      //     const matchAccountingCounts = matchAccountings.reduce((total, i) => {
      //       return parseInt(total) + parseInt(i.quantity);
      //     }, 0);

      //     printData.push({
      //       //  date:dayjs(Math.max(...endDayArr)).format("YYYY-MM-DD"),
      //       name: accountingitem,
      //       kind: matchAccountings[0].kind,
      //       start_day: dayjs(Math.min(...startDayArr)).format("YYYY-MM-DD"),
      //       end_day: dayjs(Math.max(...endDayArr)).format("YYYY-MM-DD"),
      //       unit: matchAccountings[0].unit,
      //       price: matchAccountings[0].price,
      //       quantity: matchAccountingCounts,
      //     });
      //   }
      // });
      //增加合併某些常態發生的帳,所以在處理時,要把不合併的帳都先挑出來,再把各項需要合併的帳算完後加進去~end

      // return (printData = printData.sort(function (a, b) {
      //   return parseInt(a.price) < parseInt(b.price) ? 1 : -1;
      // }));
    },
    // filterUser(list, col) {
    //   if (this.userItems.length > 0) {
    //     var rsdata = this.userItems.filter((i) => {
    //       return i.snkey == list.user_snkey;
    //     });
    //     if (rsdata.length > 0) {
    //       return rsdata[0][col];
    //     }
    //   }
    // },

    //取得取得會計帳(所有的)
    // getAccounting() {
    //   //this.$store.commit("setLoadingOn"); //讀取畫面on
    //   this.axios
    //     .post(
