    // },
    //未繳金額總計
    notPaidTotal(list) {
      var matchMonth = this.items.filter((i) => {
        return list.month > i.month && list.user_snkey == i.user_snkey;
      });

      if (matchMonth.length > 0) {
        return matchMonth.reduce((total, i) => {
          return (
            parseInt(total) + (parseInt(i.total_price) - parseInt(i.paid_price))
          );
        }, 0);
      } else {
